import React from 'react'

const interval = 10
const letter_width = 40
const speed = 10

class Canvas extends React.Component {
    state = {
        x:0,
        y:0,
        is_up:false,
        is_left:false,
        ctx: null,
    }
    
    constructor(props) {
        super(props);
        this.canvasRef = React.createRef();
    }

    getLocs = () =>{
        this.state.ctx.clearRect(0,0,this.state.ctx.canvas.width,this.state.ctx.canvas.height)
        this.state.ctx.fillStyle = "#FF0000";
        
        if (this.state.y+letter_width > this.state.ctx.canvas.height){
            this.state.is_up = true; 
        }
        if(this.state.y < 0){
            this.state.is_up = false; 
        }
        if (this.state.x+letter_width > this.state.ctx.canvas.width){
            this.state.is_left = true; 
        }
        if(this.state.x < 0){
            this.state.is_left = false; 
        }

        this.state.y = this.state.y + ((this.state.is_up)? -speed : speed);
        this.state.x = this.state.x + ((this.state.is_left)? -speed : speed);
        this.state.ctx.fillRect(this.state.x, this.state.y, letter_width, letter_width);
    }

    componentDidMount() {
        const canvas = this.canvasRef.current;
        const ctx = canvas.getContext('2d');
        this.state.ctx = ctx;
        ctx.canvas.width  = 0.8*window.innerWidth;
        ctx.canvas.height = 0.8*window.innerHeight;

        setInterval(() => {this.getLocs()
        }, interval)
    }

    render() {
      return(
        <body>
          <canvas class ="canvas" ref={this.canvasRef} />
        </body>
      )
    }
  }
  export default Canvas